import React, { useContext, useEffect, useState } from 'react';
import Header from '../components/Header/Header';
import UserForm from '../components/User/UserForm';
import * as auth from '../apis/auth';
import { LoginContext } from '../contexts/LoginContextProvider';
import { useNavigate } from 'react-router-dom';
import * as Swal from '../apis/alert';

const User = () => {

   const [userInfo, setUserInfo] = useState();
   const [delAnswer, setDelanswer] = useState(false);

   //     logout   useContext logout  
   const {logout, loginCheck, isLogin, roles, logoutSetting } = useContext(LoginContext);

   let navigate = useNavigate();

   //      403  
   useEffect(() => {
      loginCheck();
   },[]);

   /* 
           UserForm .
         page   
    */

   //   - /user/info
   const getUserInfo = async () => {

      //     /user  403    
      if(!isLogin || !roles.isUser){
         navigate("/");
         return;
      }

      const response = await auth.info();
      const data = response.data;
      console.log("getUserInfo userInfo : ----------------")
      console.log(data);

      setUserInfo(data);
   }

   //  
   const updateUser = async (form) => {
      console.log("user.jsx updateUser")
      console.log(form)

      if(form.userPw ==='' || form.email === ''){
         alert("  ")
         return;
      }

      let response;
      let data;

      try{
         response = await auth.update(form);
      }catch(error){
         console.log(`${error}`);
         console.log('    ');
         return;
      }

      data = response.data;
      const status = response.status;

      console.log(`data : ${data}`);
      console.log(`status : ${status}`);

      if(status === 200){
         console.log('  ');
         Swal.confirm("  "," ","success",(result) => {
            if(result.isConfirmed) navigate("/");
         })

         //  logout
         //logout();
      }else{
         console.log('  ');
         Swal.alert("  ","  ","error");
      }
   }

   //    
   let [userId, setUserId] = useState(); 
   /*
    state  userInfo    userInfo userId   
        . 
   */

   const deleteUser2 = async (userId, userPw) =>{ //  userId 

      if(userPw === '') {
         Swal.alert("","   ","error");
         return;
      }

      try{

         let response = await auth.login(userId, userPw);
         const status = response.status;
         
         setUserId(userId); // userId state 
         let userconfirm = window.confirm(" ?")
         if(userconfirm){
            response = await auth.remove(userId);

            alert(response.status === 200)

            Swal.alert("  "," .","error");
            setDelanswer(true); 
            navigate("/");
         }
         /* Swal.confirm(" "," ?","success",(result) => {
            if(result.isConfirmed) { 

               response = await auth.remove(userId);

               Swal.alert("  "," .","error");
               setDelanswer(true); 
               navigate("/");
               //Swal  true    Delanser  useEffect  
            }
         }); */

      }catch(error){
         //Swal.alert("","  .","error");
         Swal.alert("","   .","error");
      }
      
      /*
      setUserId(userId); // userId state 
      Swal.confirm(" "," ?","success",(result) => {
         if(result.isConfirmed) { 
            setDelanswer(true); 
            //Swal  true    Delanser  useEffect  
         }
      }); */
   }

   useEffect(() => {
      deleteUser();
   }, [delAnswer]) //Swal   deleteUser 

   // 
   const deleteUser = async () => {
    
      let response;
      let data;

      try{
         response = await auth.remove(userId);
      }catch(error){
         console.log(`${error}`);
         console.log('   .');
         return;
      }

      data = response.data;
      let status = response.status;

      if(status === 200){
         console.log(' ');
         logoutSetting();
         Swal.alert("  ","  .","success",()=>{navigate("/")});
      }else{
         console.log(' ');
         Swal.alert("  ","  .","error");
      } 
      
   }

   useEffect(() => {
      getUserInfo();
   },[])

   return (
      <>
         <Header/>
         <div className='container'>
            <UserForm userInfo={userInfo} updateUser={updateUser} deleteUser2={deleteUser2}/>
         </div>
      </>
   );
};

export default User;