import React, { useRef, useState } from 'react';
import './CommentForm.css';
import Button from 'react-bootstrap/Button';
import moment from 'moment';
import * as commentapi from '../../apis/commentapi';
import * as Swal from '../../apis/alert';

const CommentForm = (props) => { //DetailBoard.jsx  props

   const [isVisible , setIsVisible] = useState(false);

   const {comment_num, re_num, re_lev, re_step, content, loginId, user_id, reg_date, refreshPage, del} = props;
   /* lgoinId   , user_id   ,       */

   let commentText = useRef(null);

   const showReply = () => {
      if(isVisible){
         setIsVisible(false)
      }else{
         setIsVisible(true)        
      }
   }

   // 
   const deleteReply = async () => {
      
      if(loginId !== user_id){
         Swal.alert(" ","  .","warning",() => {});
         return;
      }

      let result = window.confirm(" ?");

      if(!result) return;

      let comment = {
         comment_num : comment_num,
         re_num : re_num,
         re_lev : re_lev,
         re_step : re_step,
         content : content,
         user_id : user_id 
      }

      let response = await commentapi.deleteComment(comment);

      if(response.status === 200)
         refreshPage();
   }

   // 
   const registerComment = async () => {
      let content = commentText.current.value;

      if(content === ''){
         Swal.alert(" "," .","warning",() => {});
         return;
      }

      let comment = {
         re_num : re_num,
         re_lev : re_lev,
         re_step : re_step,
         content : content,
         user_id : loginId //loginId     . user_id  
      }
      
      let response = await commentapi.registerComment(comment);

      if(response.status === 200){
         commentText.current.value = "";
         setIsVisible(false);

         refreshPage();
      }
   }

   const date = moment(reg_date).format('YYYY-HH-DD hh:mm:ss');

   return (
      <>
      <center style={{marginLeft:(re_step-1)*70}}>
      <table >
         <tr>
            {re_step === 1 ? 
            <td className='writer'>{user_id} </td>
            : 
            <td className='writer'>{user_id} </td>
            }
            
            <td className='date'>{date}</td>
         </tr>
         <tr>
            {del === 'Y' ? 
            <td><span style={{color:'red', fontSize:14, backgroundColor:'lightgray'}}> .</span> </td>
            : 
            <td colSpan="2" className='content'>{content}</td>
            }
            
         </tr>
         <tr>
            {del === 'Y' ?  
               <td></td>
            :
               <td colSpan="2">  
               <Button variant="outline-secondary" style={{width:50,height:26,fontSize:11,margin:5}} onClick={deleteReply}></Button>
               <Button variant="outline-secondary" style={{width:50,height:26,fontSize:11}} onClick={showReply}></Button>
               </td>
            }
         </tr>
      </table>
      <table style={{display: isVisible ? '' : 'none'}}>
         <tr>
            <td><input type="text" className="replyText" ref={commentText} placeholder=" ."/></td>
            <td style={{textAlign:'left'}}><button type="button" className="replyButton" onClick={() => {registerComment()}}></button></td>
         </tr>
      </table>
      </center>
   </>
   );
};

export default CommentForm;