import React, { createContext, useEffect, useState } from 'react';
import api from './../apis/api';
import Cookies from 'js-cookie';
import * as auth from '../apis/auth';
import LoginContextConsumer from './LoginContextConsumer';
import { useNavigate } from 'react-router-dom';
import * as Swal from '../apis/alert'

export const LoginContext = createContext();
LoginContext.displayName = 'LoginContextName'

/*
   1)  :       , ()    .
   2)  :            .
   3)    (  ):       ,     ID .               .
   4)  :    (  ID)  (localStorage, sessionStorage)   .
   5)  :        ,     .        .
   6)   :       ,        .

        : state   
        : state   
*/

/*
   
   -  
   - 
   -  

    
    
 */
const LoginContextProvider = ({ children }) => {

   /*
      
      -  
      -  
      -  
      -  
   */

   /*---------------------- [State] ----------------------*/

   //context value :  ,  
   const[isLogin, setLogin] = useState(false);

   // 
   const [userInfo, setUserInfo] = useState({}); //   
   localStorage.setItem('userInfo',JSON.stringify(userInfo));//userId  
   
   // 
   const [roles, setRoles] = useState({isUser : false, isAdmin : false}); //   

   // 
   const [remberUserId, setRemberUserId] = useState();

   const navigate = useNavigate();

   const [ch, setCh] = useState();

   let [tokenMsg, setTokenMsg] = useState(null);

   /* 
         jwt   , 
               
                
       jwt  
      jwt   

      /login     --- id,pw --->  server : ok
        <---jwt---      server jwt 
      /user/info --- jwt--->     server
      :jwt  header  server   
                 <--- {user} ---   server
   */

   /*
                 .
      login       .
      login -> loginCheck -> loginSetting          
        loginCheck   uesEffect . 
                 
    */
   const loginCheck = async () => {

      // jwt  
      const accessToken = Cookies.get("accessToken");
      console.log('accessToken:' + accessToken);

      //------ header jwt  ------
      //accessToken(jwt) 
      if(!accessToken){
         console.log(" accessToken ");
         setTokenMsg(" accessToken ")
         // 
         logoutSetting()
         return;
      }

      setTokenMsg(accessToken.toString());
      //accessToken(jwt)  -  header 
      api.defaults.headers.common.Authorization = `Bearer ${accessToken}`
      
      //  
      let response
      let data

      /*
                 ,
           try catch
      */ 
      try{
         response = await auth.info();
      }catch(error){
         return;
      }

      data = response.data;

      // 
      //UNAUTHORIZED :  UserController userInfo 
      if(data === 'UNAUTHORIZED' || response.status == 401){
         console.error(`accessToken (jwt)   `)
         return;
      }

      // 
      console.log(`accessToke (jwt)     !`);
      // 
      loginSetting(data, accessToken)
   }

   //  :    
   const login = async(username, password) => {
      console.log(`username : ${username}`);
      console.log(`password : ${password}`);

      try{
         const response = await auth.login(username, password);

         const data = response.data;
         const status = response.status;
         const headers = response.headers;

         const authorization = headers.authorization;
         const accessToken = authorization.replace("Bearer ", ""); //JWT, jwt     ,  loginCheck  
         
         // 
         //status 200   
         if(status === 200){
            //   , header, payload, signure 
            //  (/users/{userId} <--- userData)
            
            // accessToken(jwt)  : access ,    filter  
            //   jwt        
            Cookies.set("accessToken", accessToken)
            
            //  ( /users/{userId} <-- userData)
            loginCheck();

            Swal.alert(" ","  .","success", () => { navigate("/") });

            //  
            navigate("/");
         }
      }catch (error){
         // 
         //     

         //alert(' ');
         Swal.alert(" ","    .","error");
      }
      
   }

   // 
   const logout = () => {

      Swal.confirm(""," ","warning",
         (resuslt) => { //reuslt   true   fals 
            if(resuslt.isConfirmed){
               // 
               logoutSetting();

               // 
               navigate("/");
            }
         }
      );
}

   // 
   const loginSetting = (userData, accessToken) => { //     , assessToken => JWT 

      const {no, userId, authList} = userData;
      const roleList = authList.map((auth) => auth.auth);

      //axios  header(Authorization jwt  => Authorization : 'Bearer ${accessToken}`)
      api.defaults.headers.common.Authorization = `Bearer ${accessToken}`;

      //  : true
      setLogin(true);

      // 
      const updateUserInfo = {no, userId, roleList };
      //updateUserInfo {no: 2, userId: 'user2', roleList: Array(1)}   
      setUserInfo(updateUserInfo);
      //board       
      localStorage.setItem('updateUserInfo',JSON.stringify(updateUserInfo));

      // 
      const updateRoles = {isUser : false, isAdmin : false}

      roleList.forEach((role) => {
         if(role === 'ROLE_USER') updateRoles.isUser = true;
         if(role === 'ROLE_ADMIN') updateRoles.isAdmin = true; 
      });

      setRoles(updateRoles);
   }

   // 
   const logoutSetting = () => {

      //axios  
      api.defaults.headers.common.Authorization = undefined;

      // 
      Cookies.remove("accessToken");

      //  : false
      setLogin(false);

      //  
      setUserInfo(null);

      //  
      setRoles(null);

      //   
      localStorage.removeItem('userInfo');
      localStorage.removeItem('updateUserInfo');
      localStorage.removeItem('no');
      localStorage.removeItem('totalPage');
      localStorage.removeItem('page');
   }
 
   useEffect(() => {
      //  -          .     DOM    .    
      loginCheck();
   },[])
   
   return (
      <div>
         <LoginContext.Provider value={{isLogin, userInfo, roles, login, logout, loginCheck, logoutSetting, tokenMsg}}>
            {children}
         </LoginContext.Provider>
      </div>
   );
};

export default LoginContextProvider;