
import { useEffect, useRef, useState } from 'react';
import Header from '../components/Header/Header';
/* import { Map, MapMarker } from "react-kakao\-maps-sdk" */
import Location from '../components/Map/Location';
import useCurrentLocation from '../hooks/useCurrentLocation';
import { geolocationOptions } from '../constants/geolocationOptions';
import api from '../apis/api';


const {kakao} = window;

const MapSearch = () => {
   
   //   
   const { location: currentLocation, error: currentError } = useCurrentLocation(geolocationOptions);
   
   const [isOpen, setIsOpen] = useState(); //    

   //    
   const [state, setState] = useState({
      center: { lat: 37.4923615, lng: 127.0292881 },
      //    panto    : panto  
      isPanto: false,
   })

   const [data, setData] = useState({
      leve : 0,
      position : {
         lat : 0,
         lng : 0
      }
   })

   useEffect(() => {

      getPhamarcyData();
      
   },[])
   

     // api 
   const getPhamarcyData = async () => {
      
      let testUrl = encodeURI("http://safemap.go.kr/openApiService/data/getPharmacyData.do?serviceKey=AH80S7N7-AH80-AH80-AH80-AH80S7N7P3&pageNo=1&numOfRows=2000&type=JSON");

      /* 
      100 1 
      row 10 : 0 ~ 300
      row 20 : 0 ~ 600 
      row 30 : 0 ~ 900 
      row 40 : 0 ~ 1200

      row 100 : 0 ~ 3200
      row 1000 : 0 ~ 30000
      row 2000 : 0 ~ 60000
      */
      let data = await api.post(testUrl.toString());
      console.log(data);
   }
   
   const markerData = [
      { lat: 37.5665, lng: 126.9780, name: '' },
      { lat: 35.1796, lng: 129.0756, name: '' },
      { lat: 37.4563, lng: 126.7052, name: '' },
      { lat: 35.1595, lng: 126.8526, name: '' },
      { lat: 35.8722, lng: 128.6014, name: '' },
    ];

   return (
      <>
      <Header/>
      <Location location={currentLocation} error={currentError} />
      <div className='mapContainer'>

      
      </div>
      </>
   );
};

export default MapSearch;


